import { getUserById, statusUser } from '../api/api';
const ADD_POST = 'ADD_POST';
const SET_PROFILE = 'SET_PROFILE';
const SET_STATUS_USER = 'SET_STATUS_USER';
const DELETE_POST = 'DELETE_POST';

let initialState = {
    posts: [
        { countLikes: '1', message: 'post1', id: '1', http: 'https://s.yimg.com/ny/api/res/1.2/i4kpzp4ejlsUL1kEfzQGEg--~A/YXBwaWQ9aGlnaGxhbmRlcjtzbT0xO3c9ODAw/https://img.huffingtonpost.com/asset/5cd29f66200000590016e7b8.jpeg' },
        { countLikes: '2', message: 'post2', id: '2', http: 'data:image/jpeg;base64,' },
        { countLikes: '3', message: 'post3', id: '3', http: 'https://cdn2.iconfinder.com/data/icons/circle-avatars-1/128/050_girl_avatar_profile_woman_suit_student_officer-512.png' },
        { countLikes: '4', message: 'post4', id: '4', http: 'https://dt2sdf0db8zob.cloudfront.net/wp-content/uploads/2019/12/9-Best-Online-Avatars-and-How-to-Make-Your-Own-for-Free-image1-5.png' },
        { countLikes: '5', message: 'post5', id: '5', http: 'https://store.playstation.com/store/api/chihiro/00_09_000/container/IL/en/999/EP0149-CUSA09988_00-AV00000000000002/1553528383000/image?w=240&h=240&bg_color=000000&opacity=100&_version=00_09_000' },
    ],
    profile: {},
    status: ''
};

const profilesReducer = (state = initialState, action) => {
    let newPost = {
        countLikes: '0', message: action.newPostText, id: '6',
        http: 'https://cdn2.iconfinder.com/data/icons/circle-avatars-1/128/050_girl_avatar_profile_woman_suit_student_officer-512.png'
    };
    switch (action.type) {
        case ADD_POST:
            if (newPost.message !== '')
                return {
                    ...state,
                    posts: [...state.posts, newPost]
                };
        case SET_PROFILE:
            return {
                ...state,
                profile: action.profile
            };
        case SET_STATUS_USER:
            return {
                ...state,
                status: action.status
            };
        case DELETE_POST:
            return {
                ...state, posts: state.posts.filter(p => p.id !== action.postId)
            };
        default:
            return state;
    }
}

export const createAddPostAction = (newPostText) => { return { type: ADD_POST, newPostText } }
export const createDeletePostAction = (postId) => { return { type: DELETE_POST, postId } }
export const setProfile = (profile) => { return { type: SET_PROFILE, profile } }
export const setStatusUser = (status) => { return { type: SET_STATUS_USER, status } }

export const getUserThunk = (userId) => async (dispatch) => {
    let data = await getUserById(userId);
    dispatch(setProfile(data))
}

export const getStatusUserThunk = (userId) => async (dispatch) => {
    let data = await statusUser.getStatusUser(userId);
    dispatch(setStatusUser(data))
}

export const updateStatusUserThunk = (status) => async (dispatch) => {
    let data = await statusUser.updateStatusUser(status);
    if (data.resultCode === 0) { dispatch(setStatusUser(status)) }
}

export default profilesReducer;